interface ListInt{
  name:string
  roleId:number
  roleList?:ListInt[]
  viewRole?:string
}

class InitData {
  id:number
  authority:number[]
  list:ListInt[]=[]
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  treeRef:any
  constructor (id:number, authority:number[]) {
    this.id = id
    this.authority = authority
  }
}
export { InitData, ListInt }
